import axios from "axios"

function fetch(url,data){
    return new Promise((resolve,reject)=>{
        axios.get(url,data)
        .then(res => {
            // if(res.data.status === 200){
            //     resolve(res)
            // }else{
            //     reject(res)
            // }
            resolve(res)
        })
        .catch(err=>{
            reject(err)
        })
    })
}

export default fetch
